import { Routes, Route } from "react-router-dom";
import Home from "./Home";

import Etm1 from "./slide/explain/entanglement/entangle";
import Qubit from "./slide/explain/qubit/qubit";
import Sp1 from "./slide/explain/superposition/superpsotion"; // sp1   ( )
import QuantumComputing from "./slide/explain/concept/compute"; //    
import Difference from "./slide/explain/concept/difference";
import History from "./slide/explain/concept/history"; //     
import Applications from "./slide/explain/concept/applications"; //     


function AppRoutes() {
  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/entangle" element={<Etm1 />} /> {/* entangle   */}      
      <Route path="/qubit" element={<Qubit />} /> {/* qubit   */}          
      <Route path="/sp1" element={<Sp1 />} /> {/* sp1   ( ) */}
      <Route path="/superposition" element={<Sp1 />} /> {/* superposition   */}
      <Route path="/compute" element={<QuantumComputing />} /> {/*  ?   */}
      <Route path="/compute/difference" element={<Difference />} /> {/*        */}
      <Route path="/compute/history" element={<History />} /> {/*      */}
      <Route path="/compute/applications" element={<Applications />} /> {/*      */}
      {/*     */}
    </Routes>
  );
}

export default AppRoutes;